<template>
  <div class="wider">
    <el-container>
      <el-container>
        <el-main>Main</el-main>
        <el-aside width="400px">
          <div class="login">
            <el-form
              :model="ruleForm"
              :rules="rules"
              ref="ruleForm"
              label-width="95px"
              class="demo-ruleForm"
              @submit.native.prevent
            >
              <el-form-item label="用户名" prop="id">
                <el-input v-model="ruleForm.id" placeholder="请输入用户名"></el-input>
              </el-form-item>
              <el-form-item label="密码" prop="password">
                <el-input show-password v-model="ruleForm.password" placeholder="请输入密码"></el-input>
              </el-form-item>
              <el-form-item>
                <el-button native-type="submit" type="primary" @click="submitForm('ruleForm')">登录</el-button>
                <el-button @click="resetForm('ruleForm')">重置</el-button>
              </el-form-item>
            </el-form>
          </div>
        </el-aside>
      </el-container>
    </el-container>
  </div>
</template>

<script>
export default {
  
  data() {
    return {
      ruleForm: {
        id: "",
        password: ""
      },
      returnMessage: "",
      rules: {
        id: [{ required: true, message: "请输入用户名", trigger: "blur" }],
        password: [{ required: true, message: "请输入密码", trigger: "blur" }]
      }
    };
  },
  
  methods: {
    submitForm(formName) {
      //console.log(this.ruleForm.id);
      //console.log(this.ruleForm.password);
      this.$refs[formName].validate(valid => {
        if (valid) {
          var that = this;
          this.$http
            .post("http://localhost:8989/teacher/login", that.ruleForm)
            .then(res => {
              // console.log(that.ruleForm);
              // console.log(res.data.msg);
              that.returnMessage = res.data.msg;
              this.$message(that.returnMessage);
              if (res.data.code == 200) {
                that.getDescribe(that.ruleForm.id,res.data.code);
              }
              that.resetForm("ruleForm");
              var isError = document.getElementsByClassName("demo-ruleForm");
              isError[0].querySelector("input").focus();
            })
            .catch(err => {
              //console.log(that.ruleForm);
              that.resetForm("ruleForm");
              this.$message("登录出现错误，请联系后台管理员！");
            });
        } else {
          setTimeout(() => {
            var isError = document.getElementsByClassName("demo-ruleForm");
            isError[0].querySelector("input").focus();
          }, 100);
          return false;
        }
      });
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
    getDescribe(id,code) {
      //   直接调用$router.push 实现携带参数的跳转
      // this.$router.push({
      //   path: '/index/main',
      // })

      //将用户名放入sessionStorage
      sessionStorage.setItem("userid", id);
      sessionStorage.setItem("userToken", code);
      //将用户名放入vuex
      this.$store.dispatch("setUser", id);
       this.$store.dispatch("setToken", code);
       console.log(this.$store.state.isLogin)
      //路由跳转
      this.$router.push("/index/main");
      
    }
  }
};
</script>


<style scoped>
.login {
  margin-top: 50%;
  margin-right: 20%;
}
.wider {
  height: 100%;
}
.el-container {
  height: 100%;
}
.el-aside {
  background-color: #d3dce6;
  color: #333;
  text-align: center;
  line-height: 600px;
}

.el-main {
  background-color: #e9eef3;
  color: #333;
  text-align: center;
  line-height: 600px;
}
</style>
